ature cards\n.creatix-features {\n  background-color: rgba(243, 243, 243, 1);\n  padding: 3rem;\n\n  .creatix-card {\n    text-align: center;\n    border: 1px solid;\n    border-color: transparent;\n    border-radius: 5px;\n    margin-bottom: 10rem;\n  }\n  @media screen and (max-width: 39.9375em) {\n    padding: 0;\n    padding-top: 3rem;\n  }\n}\n\n.feature-card {\n  @extend .margin-bottom-l;\n\n  color: $creatix-white-light-blue;\n  text-align: center;\n\n  &__padding {\n    padding: 1rem;\n    padding-top: 2rem;\n    padding-bottom: 2rem;\n  }\n\n  &__title {\n    @extend .padding-bottom-m;\n  }\n\n  &--blue {\n    background-color: $creatix-blue;\n  }\n\n  &--pink {\n    background-color: $creatix-pink;\n  }\n\n  &--gray {\n    background-color: $creatix-gray2;\n  }\n}\n",".header {\n  padding-bottom: 1rem;\n  padding-top: 1rem;\n  font-family: 'Creatix Medium';\n}\n\n.header-inner {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  max-width: 60rem;\n  width: 90%;\n}\n\n.site-nav {\n  .nav-list {\n    align-items: center;\n    list-style: none;\n    margin: 0;\n    padding: 0;\n\n    .nav-item {\n      margin-top: 1rem;\n    }\n  }\n}\n\n.nav-link {\n  color: $creatix-pink;\n  position: relative;\n  display: inline-block;\n}\n\n.nav-link__active {\n  color: $creatix-white-deep-blue;\n  position: relative;\n  display: inline-block;\n}\n\n.side-menu {\n  position: sticky;\n  top: 0;\n  height: 100%;\n  top: 25%;\n}\n\n.login-modal {\n  position: fixed; /* Stay in place */\n  z-index: -1; /* Sit on top */\n  width: 100%;\n  //height: 100%;\n  //overflow: hidden;\n\n  .login-box {\n    border-radius: 10px;\n    border: 1px solid;\n    border-color: $electron-blue;\n    padding: 2rem;\n    background-color: $creatix-pink;\n  }\n}\n",".footer-nav {\n  .footer-list {\n    list-style: none;\n\n    .footer-item {\n      font-family: 'Creatix Light';\n      color: $peach;\n    }\n  }\n}\n\n.footer {\n  margin-top: 2rem;\n  padding-bottom: 2